.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*------------------------------------*    #tools.pxh-animations
\*------------------------------------*/
/*------------------------------------*    #tools.pxh-mixins
\*------------------------------------*/
/*------------------------------------*    #elements.body
\*------------------------------------*/
html {
  font-size: 0.9375em;
  line-height: 1.3333333333;
  background-color: #525156;
  color: black;
}

html, body {
  font-family: "GE Inspira Sans", "Helvetica", "Arial", sans-serif;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  min-height: 100%;
}

/*------------------------------------*    #elements.links
\*------------------------------------*/
a {
  color: #3e87e8;
}

/*------------------------------------*    #elements.hr
\*------------------------------------*/
hr {
  height: 1px;
  color: #9999a3;
  background: #9999a3;
  font-size: 0;
  border: 0;
  margin-top: 15px;
  margin-bottom: 15px;
}

/*------------------------------------*    #elements.typography
\*------------------------------------*/
/* Text-level semantics
   ========================================================================== */
/**
 * Address styling not present in IE 8/9/10/11, Safari, and Chrome.
 */
abbr[title] {
  border-bottom: 1px dotted;
}

/**
 * Address style set to `bolder` in Firefox 4+, Safari, and Chrome.
 */
b,
strong {
  font-weight: bold;
}

/**
 * Address styling not present in Safari and Chrome.
 */
dfn {
  font-style: italic;
}

/**
 * Address styling not present in IE 8/9.
 */
mark {
  background: #ff0;
  color: #000;
}

/**
 * Address inconsistent and variable font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` affecting `line-height` in all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

html {
  background-color: #242326;
  color: white;
}

a {
  text-decoration: none;
}

/*------------------------------------*    #components.pxh-error
\*------------------------------------*/
.pxh-error {
  width: 100%;
  max-width: 1200px;
  text-align: center;
  margin: 90px 0 0 0;
  padding: 30px 15px 120px 15px;
}

@media (min-width: 48em) {
  .pxh-error\@md {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
  }
}

@media (min-width: 64em) {
  .pxh-error\@lg {
    margin-left: auto;
    margin-right: auto;
    width: 70%;
  }
}

.pxh-error__icon {
  font-size: 60px;
  color: #b1b1bc;
  line-height: 1;
}

.pxh-error__title {
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.pxh-error__title a,
.pxh-error__title a:link {
  text-decoration: underline;
}

.pxh-error__description {
  font-size: 15px;
  color: white;
}

.pxh-error__description a,
.pxh-error__description a:link {
  text-decoration: underline;
}
